.articles {
  margin: 0 0;
  padding-top: 87px;
  padding-bottom: 160px;
}


.articles__title {
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  color: #1C1C1C;
  padding-bottom: 42px;
  margin: 0 0;
}
.articles__wrapper{
display: grid;
gap: 30px;
grid-template-columns: repeat(auto-fill, 570px);
justify-content: center;
}
