.form-join-us {
  display: grid;
  grid-template-columns: 250px 250px 250px 250px;
  gap: 19px;
}

.form-join-us__left {
  max-width: 521px;
}

.form-join-us__right {
  max-width: 520px;
}

.form-join-us__input {
  border: 1px solid #DADADA;
  box-sizing: border-box;
  border-radius: 50px;
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  /* identical to box height, or 29px */
  color: #787878;
  padding: 15px 28px;
  margin: 0;
  max-height: 60px;
}

.form-join-us__option {
  &:hover {
    background: #3172B9;
    color: white;
  }
}

.form-join-us__textarea {
  grid-column: 3/5;
  grid-row: 1/3;
  width: 100%;
  padding: 0;
  border: 1px solid #DADADA;
  box-sizing: border-box;
  border-radius: 20px;
  max-width: 520px;
  min-height: 139px;

}

.form-join-us__button-group {
  grid-column: 4/5;
  grid-row: 3/4;
}

.form-join-us__button {
  display: block;
  background: #3172B9;
  border-radius: 50px;
  border: none;
  margin: 0;
  padding: 20px 67px;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;

  color: #FFFFFF;
}

.form-join-us__checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 8px;
}

.form-join-us__agreement {
  margin-left: 11px;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #787878;
}
