.articles-card {
  display: flex;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;
}

.articles-card__img {
  border-radius: 5px 0px 0px 5px;
}
.articles-card__content{
  padding: 23px 30px;
}
.articles-card__date-pub {
  margin: 0 0;
  font-weight: normal;
  font-size: 10px;
  line-height: 150%;
  /* identical to box height, or 15px */
  color: #787878;
}

.articles-card__title {
  font-weight: 600;
  font-size: 20px;
  line-height: 130%;
  /* identical to box height, or 26px */
  color: #1C1C1C;
  padding-bottom: 15px;
  margin: 0 0;
}
.articles-card__text{
  margin: 0 0;
  font-weight: normal;
font-size: 14px;
line-height: 150%;
/* or 21px */
color: #787878;
}
