.footer {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 45px 0;
}

.footer__logo {
  margin-right: 30px;
}

.footer__logo-copyright {
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #787878;
  margin-right: auto;
}

.nav-footer__list {
  display: flex;
  padding-left: 0;
}

.nav-footer__item {
  margin-right: 28px;
}

.nav-footer__link {
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #787878;
  text-decoration: none;
}

.footer__social {
  display: flex;
  margin-left: 28px;
  padding-left: 0;
}

.social__item:not(:last-child) {
  margin-right: 32px;
}
