.services {
  padding-top: 87px;
  padding-bottom: 160px;
}

.services__title,
.services__text {
  text-align: center;
}

.services__title {
  padding-bottom: 21px;
  margin: 0 0;
}

.services__text {
  margin: 0 0;
  padding-bottom: 54px;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  /* identical to box height */
  color: #787878;
}

.services__wrapper-card {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(auto-fill, 270px);
  justify-content: center;


}
