.sentence {
  background-image: url("../images/contents/first-bg1.webp");
  background-repeat: no-repeat;
  background-position: right;
  max-height: 550px;
  padding-top: 113px;
  padding-bottom: 137px;
}

.sentence__wrapper {
  max-width: 530px;
}

.sentence__title {
  font-weight: 300;
  font-size: 40px;
  line-height: 130%;
  /* or 52px */
  color: #1C1C1C;
  margin-bottom: 26px;
}

.sentence__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 155%;
  /* or 25px */
  color: #787878;
  margin-bottom: 34px;
}

.sentence-button__text {
  font-weight: normal;
  font-size: 11px;
  line-height: 155%;
  /* identical to box height, or 17px */
  text-transform: uppercase;
  color: #787878;
}
.sentence-button__descryption{
  margin-top: 29px;
}
.sentence-button__wrapper{
  display: inline-block;
  text-align: center;
}
