.button {
  padding: 15px 35px;
  background: #3172B9;
  border-radius: 3px;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #FFFFFF;
  text-decoration: none;
  &:hover {
    background: #71A7E2;
  }

  &:focus {
    background: #3172B9;
    border: 1px solid #71A7E2;
    box-sizing: border-box;
  }
  &:active{
    background: #0E3B6C;
  }
  &:disabled{
    background: #C4C4C4;
  }
}
