.card-advantage {
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 62px 30px;
  max-width: 270px;
  text-align: center;
}

.card-advantage__title {
  padding-top: 27px;
  padding-bottom: 15px;
  margin: 0 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 130%;
  /* identical to box height, or 26px */
  color: #1C1C1C;
}

.card-advantage__text {
  margin: 0 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  /* or 21px */
  color: #787878;
}
