.clients {
  margin: 0 0;
  padding-top: 88px;
  padding-bottom: 160px;
}

.clients__title {
  margin: 0 0;
  padding-bottom: 42px;
  font-weight: 300;
  font-size: 36px;
  line-height: 130%;
  /* identical to box height, or 47px */
  color: #1C1C1C;
}
.clients__wrapper-card{
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(auto-fill, 370px);
  justify-content: center;
}
