.client-card {
  margin: 0 0;
  border: 1px solid #E4E4E4;
  box-sizing: border-box;
  border-radius: 5px;
  padding-bottom: 32px;
  padding-top: 51px;
  padding-left: 36px;
  padding-right: 37px;
}

.clients-card__text {
  font-weight: normal;
  font-size: 14px;
  line-height: 180%;
  /* or 25px */
  color: #787878;
  margin: 0 0;
  margin-bottom: 33px;
}

.client-card__content {
  display: flex;
  align-items: center;
  margin: 0 0;
  max-width: 297px;
  border-top: 1px solid #DADADA;
  padding-top: 28px;
}

.client-card__img {
  border-radius: 50%;
  border: 2px solid #3172B9;
  max-width: 67px;
  max-height: 67px;
  margin-right: 17px;
}

.clients-card__client-name {
  font-weight: 600;
  font-size: 17px;
  line-height: 180%;
  /* identical to box height, or 31px */
  color: #1C1C1C;
  margin: 0 0;
}
