.main-nav {
  margin: 0 0;
}

.main-nav__list {
  display: flex;
}

.main-nav__item {
  margin-right: 52px;
}

.main-nav__link {
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #1C1C1C;
  text-decoration: none;
  cursor: pointer;
  &--active {
    color: #3172B9;
    border-bottom: 2px solid #3172B9;
  }
&:hover{
  color: #71A7E2;
}

}
