.contacts{
  margin: 0 0;
  padding-top: 87px;
  padding-bottom: 160px;
}

.contacts__title{
  font-weight: 300;
font-size: 36px;
line-height: 130%;
/* identical to box height, or 47px */
color: #1C1C1C;
margin-top: 19px;
margin-bottom: 109px;
}

.contacts__wrapper{
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  justify-content: space-between;
}
.contacts__title{
  display: block;
  font-weight: 600;
font-size: 17px;
line-height: 180%;
/* identical to box height, or 31px */
color: #1C1C1C;
margin-top: 19px;
margin-bottom: 30px;
}
.contacts__text{
margin: 0 0;
}
.contact__svg{
  display: block;
}
